import { Node, Inline, Block } from '@ali/4ever-cangjie';
import { isPureParagraphValue, Paragraph } from '@ali/4ever-plugin-paragraph';
import { Mention } from '@ali/4ever-plugin-mention';
import { Table, TableRow, TableCell } from '@ali/4ever-plugin-table';
import { List } from '@ali/4ever-plugin-list';

export function isListValue(node: Node): node is Block {
  // list 数据类型为 paragraph，区别在于 data 中存在 list 字段
  return List.isList(node);
}

export function isMentionValue(node: Node): node is Inline {
  return Mention.isMention(node);
}

export function isParagraphValue(node: Node): node is Block {
  return Paragraph.isParagraph(node);
}

export { isPureParagraphValue };

export function isTableValue(node: Node): node is Block {
  return Table.isTable(node);
}

export function isTableRowValue(node: Node): node is Block {
  return TableRow.isTableRow(node);
}

export function isTableCellValue(node: Node): node is Block {
  return TableCell.isTableCell(node);
}
